.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  width: 100%;
  max-width: 400px;
  padding: 20px;

  background-color: var(--color-blue);
  border: 2px solid var(--color-brand-light);
}

.title {
  margin: 0;
  margin-bottom: 20px;

  font-size: var(--title-fz);
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;

  margin-bottom: 20px;
}

.header h2 {
  margin-bottom: 0;
}

.close-button svg {
  width: 30px;
  height: 30px;
}
